import React, { useState, useRef, useEffect } from 'react';
import VideoPlayer from './components/VideoPlayer';
import GifOverlay from './components/GifOverlay';
import GifControls from './components/GifControls';
import GifSearch from './components/GifSearch';
import { FFmpeg } from '@ffmpeg/ffmpeg';
import { fetchFile } from '@ffmpeg/util';
import { parseGIF, decompressFrames } from 'gifuct-js';
import './App.css';

const App = () => {
  //  GIF  GIF 
  const [gifs, setGifs] = useState([]);
  const [currentTime, setCurrentTime] = useState(0);
  const [videoDuration, setVideoDuration] = useState(0);
  const [videoUrl, setVideoUrl] = useState(null);
  const [selectedGifId, setSelectedGifId] = useState(null);
  //  metadata 
  const [videoDimensions, setVideoDimensions] = useState({ width: 640, height: 360 });
  // 
  const [previewDimensions, setPreviewDimensions] = useState({ width: 640, height: 360 });

  // FFmpeg 
  const [isExporting, setIsExporting] = useState(false);
  const [ffmpegInstance, setFfmpegInstance] = useState(null);
  const [realRatio, setRealRatio] = useState(0);   // FFmpeg  (0~1)
  const [ffmpegTime, setFfmpegTime] = useState(0);   // FFmpeg 

  // 
  const hiddenVideoRef = useRef(null);
  const previewContainerRef = useRef(null);

  // 
  useEffect(() => {
    const updatePreviewDimensions = () => {
      if (previewContainerRef.current) {
        setPreviewDimensions({
          width: previewContainerRef.current.clientWidth,
          height: previewContainerRef.current.clientHeight,
        });
      }
    };
    updatePreviewDimensions();
    window.addEventListener('resize', updatePreviewDimensions);
    return () => {
      window.removeEventListener('resize', updatePreviewDimensions);
    };
  }, [videoDimensions]);

  const handleVideoLoaded = (duration, width, height) => {
    setVideoDuration(duration);
    setVideoDimensions({ width, height });
  };

  const handleSelectGif = (id) => {
    setSelectedGifId(id);
  };

  const handleDeleteVideo = () => {
    setVideoUrl(null);
    setGifs([]);
    setVideoDuration(0);
  };

  const handleCancelExport = () => {
    if (ffmpegInstance) {
      try {
        ffmpegInstance.terminate();
      } catch (err) {
        console.error('FFmpeg :', err);
      }
    }
    setRealRatio(1);
    setIsExporting(false);
  };

  const handleLocalVideoTimeUpdate = () => {
    const hiddenVideo = hiddenVideoRef.current;
    if (!hiddenVideo || videoDuration <= 0) return;
    // 
  };

  const startLocalDecoding = () => {
    const hiddenVideo = hiddenVideoRef.current;
    if (hiddenVideo) {
      hiddenVideo.currentTime = 0;
      hiddenVideo.playbackRate = 4.0;
      hiddenVideo.play().catch((err) => {
        console.error(' Video :', err);
      });
    }
  };

  const stopLocalDecoding = () => {
    const hiddenVideo = hiddenVideoRef.current;
    if (hiddenVideo) {
      hiddenVideo.pause();
      hiddenVideo.currentTime = 0;
    }
  };

  //  gifuct-js  GIF 
  const getGifDuration = async (gifUrl) => {
    try {
      const response = await fetch(gifUrl);
      const buffer = await response.arrayBuffer();
      const parsedGif = parseGIF(buffer);
      const frames = decompressFrames(parsedGif, true);
      const totalDuration = frames.reduce((acc, frame) => acc + frame.delay * 10, 0);
      return totalDuration / 1000;
    } catch (error) {
      console.error(' GIF :', error);
      return 0;
    }
  };

    // 
const handleExportVideo = async () => {
  if (!videoUrl) {
    alert('');
    return;
  }
  
  const ffmpeg = new FFmpeg({
    log: true,
    corePath: '/ffmpeg-core.js',
    logger: ({ type, message }) => {
      if (type === 'stderr') {
        console.error(`[FFmpeg ${type}] ${message}`);
      } else {
        console.log(`[FFmpeg ${type}] ${message}`);
      }
    },
  });
  
  console.log('========  ========');
  try {
    setIsExporting(true);
    setRealRatio(0);
    setFfmpegTime(0);
    setFfmpegInstance(ffmpeg);
    
    startLocalDecoding();
    
    console.log(' FFmpeg ...');
    await ffmpeg.load();
    console.log('FFmpeg ');
    
    ffmpeg.on("progress", ({ time }) => {
      if (videoDuration > 0) {
        const timeInSec = time / 1000000;
        const ratio = timeInSec / videoDuration;
        const clampedRatio = Math.min(Math.max(ratio, 0), 1);
        setRealRatio(clampedRatio);
        setFfmpegTime(timeInSec);
        console.log(`FFmpeg : ${timeInSec.toFixed(2)}s, ratio=${clampedRatio}`);
      }
    });
    
    const videoFileName = 'input.mp4';
    console.log(':', videoUrl);
    await ffmpeg.writeFile(videoFileName, await fetchFile(videoUrl));
    console.log(' (input.mp4)');
    
    let ffmpegArgs;
    if (gifs.length === 0) {
      //  GIF 
      ffmpegArgs = [
        '-y',
        '-i', videoFileName,
        '-preset', 'fast',
        '-t', `${videoDuration}`,
        '-c:v', 'libx264',
        '-c:a', 'aac',
        '-copyts',
        'output.mp4',
      ];
      console.log(' GIF FFmpeg ', ffmpegArgs.join(' '));
    } else {
      // 
      const scaleFactor = videoDimensions.width / previewDimensions.width;
      console.log('scaleFactor:', scaleFactor);
      const gifInputs = [];
      const filterComplexParts = [];
      let lastStream = '0:v';
      
      for (let i = 0; i < gifs.length; i++) {
        const gif = gifs[i];
        const gifFileName = `gif${i}.gif`;
        console.log(` GIF [${i}] :`, gif.url);
        const gifData = await fetchFile(gif.url);
        await ffmpeg.writeFile(gifFileName, gifData);

        const gifDuration = await getGifDuration(gif.url);
        if (!gifDuration) {
          throw new Error(' GIF ');
        }
        console.log(`GIF [${i}] : ${gifDuration} `);

        // 
        const exportX = gif.transform.x * scaleFactor;
        const exportY = gif.transform.y * scaleFactor;
        const exportGifScale = gif.transform.scale * scaleFactor;
        
        //  -ignore_loop 0  GIF 
        gifInputs.push('-ignore_loop', '0', '-i', gifFileName);
        
        //  ( PI  3.14159)
        filterComplexParts.push(
          `[${i + 1}:v] scale=iw*${exportGifScale}:ih*${exportGifScale},` +
          `rotate=${gif.transform.rotate}*3.14159/180:ow=rotw(${gif.transform.rotate}*3.14159/180):oh=roth(${gif.transform.rotate}*3.14159/180):c=none [gif${i}]`
        );
        //  exportX  exportY  offset 
        filterComplexParts.push(
          `[${lastStream}][gif${i}] overlay=` +
          `x=${exportX}:y=${exportY}:enable='between(t,${gif.startTime},${gif.endTime})' [overlay${i}]`
        );
        lastStream = `overlay${i}`;
      }
      
      const filterComplex = filterComplexParts.join('; ');
      console.log('filter_complex:', filterComplex);
      
      ffmpegArgs = [
        '-y',
        '-i', videoFileName,
        ...gifInputs,
        '-filter_complex', filterComplex,
        '-map', `[${lastStream}]`,
        '-map', '0:a',
        '-preset', 'fast',
        '-t', `${videoDuration}`,
        '-c:v', 'libx264',
        '-c:a', 'aac',
        '-copyts',
        '-vsync', 'vfr',
        'output.mp4',
      ];
      console.log(' FFmpeg ', ffmpegArgs.join(' '));
    }
    
    await ffmpeg.exec(ffmpegArgs);
    console.log('FFmpeg ');
    
    stopLocalDecoding();
    
    let data;
    try {
      data = await ffmpeg.readFile('output.mp4');
      console.log(' output.mp4 :', data.byteLength, 'bytes');
    } catch (error) {
      console.error(' output.mp4 ', error);
      alert(' Console ');
      return;
    }
    
    const url = URL.createObjectURL(new Blob([data.buffer], { type: 'video/mp4' }));
    const a = document.createElement('a');
    a.href = url;
    a.download = 'output.mp4';
    a.click();
    
    console.log('');
    alert('');
  } catch (error) {
    console.error(':', error);
    if (error.message?.includes('terminated')) {
      alert(' FFmpeg ');
    } else {
      alert(`: ${error.message}`);
    }
  } finally {
    console.log('========  ========');
    setRealRatio(1);
    stopLocalDecoding();
    setIsExporting(false);
    setFfmpegInstance(null);
  }
};



  
  
  const displayProgress = realRatio * 100;

  //  (50vh) 
  const previewContainerStyle = videoDimensions.height
    ? {
        position: 'relative',
        height: '50vh',
        width: `${(videoDimensions.width / videoDimensions.height) * (window.innerHeight * 0.5)}px`,
        margin: '0 auto',
      }
    : {
        position: 'relative',
        height: '50vh',
        width: 'auto',
      };

  return (
    <div className="App">
      <div className="button-container">
        <button onClick={() => document.getElementById('video-upload').click()} disabled={isExporting}>
          
        </button>
        <button onClick={() => alert(' GIF')} disabled={isExporting}>
           GIF
        </button>
        <button onClick={handleDeleteVideo} disabled={isExporting}>
          
        </button>
        <button onClick={handleExportVideo} disabled={isExporting}>
          
        </button>
        <input
          id="video-upload"
          type="file"
          accept="video/*"
          style={{ display: 'none' }}
          onChange={(e) => {
            const file = e.target.files[0];
            if (file) {
              const url = URL.createObjectURL(file);
              setVideoUrl(url);
            }
          }}
        />
      </div>
  
      {isExporting && (
        <div className="modal-backdrop">
          <div className="modal-content">
            <h3>...</h3>
            <p>{displayProgress.toFixed(2)}%</p>
            <p>FFmpeg {ffmpegTime.toFixed(2)} </p>
            <button onClick={handleCancelExport}></button>
          </div>
        </div>
      )}
  
      {/*  50vh */}
      <div ref={previewContainerRef} className="video-container" style={previewContainerStyle}>
        {videoUrl && (
          <VideoPlayer
            videoUrl={videoUrl}
            onTimeUpdate={setCurrentTime}
            onDurationLoaded={handleVideoLoaded}
          />
        )}
        {gifs.map((gif) => (
          <GifOverlay
            key={gif.id}
            gif={gif}
            currentTime={currentTime}
            videoDuration={videoDuration}
            onUpdate={(newGif) =>
              setGifs((prevGifs) =>
                prevGifs.map((g) => (g.id === newGif.id ? newGif : g))
              )
            }
            onSelect={handleSelectGif}
            isSelected={selectedGifId === gif.id}
          />
        ))}
      </div>
  
      <GifControls
        gifs={gifs}
        selectedGifId={selectedGifId}
        videoDuration={videoDuration}
        onUpdate={(updatedGif) =>
          setGifs(gifs.map((g) => (g.id === updatedGif.id ? updatedGif : g)))
        }
      />
  
      <GifSearch
        onSelectGif={(gifUrl) => {
          const newGif = {
            id: Date.now(),
            url: gifUrl,
            startTime: 0, // GIF 
            endTime: videoDuration, // GIF 
            transform: { x: 100, y: 100, scale: 1, rotate: 0 },
          };
          setGifs([...gifs, newGif]);
          setSelectedGifId(newGif.id);
        }}
      />
  
      {/*  */}
      <video
        ref={hiddenVideoRef}
        src={videoUrl || null}
        style={{ width: '1px', height: '1px', opacity: 0 }}
        onTimeUpdate={handleLocalVideoTimeUpdate}
        onLoadedMetadata={() => {
          if (hiddenVideoRef.current) {
            hiddenVideoRef.current.playbackRate = 4.0;
            const videoWidth = hiddenVideoRef.current.videoWidth;
            const videoHeight = hiddenVideoRef.current.videoHeight;
            setVideoDimensions({ width: videoWidth, height: videoHeight });
          }
        }}
      />
    </div>
  );
};

export default App;
